@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fraunces:wght@700;900&display=swap");
@media (max-width: 35.999em) {
  .img__backgroundorange {
    background-image: url(/img/mobile-orange.jpg);
  }
}
@media (min-width: 36em) {
  .img__backgroundorange {
    background-image: url(/img/mobile-orange.jpg);
  }
}
@media (min-width: 62em) {
  .img__backgroundorange {
    background-image: url(/img/desktop-header.jpg);
  }
}
.navbar__menu {
  background-color: #3EBFFF;
}

.navbar-dark .navbar-toggler-icon {
  background-image: url(/img/list.svg);
}

.a__brand {
  display: flex;
  justify-content: center;
  align-content: center;
}

.navbar-dark .navbar-toggler {
  border-color: transparent !important;
}

.navbar-nav > li {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 1.875rem;
}

.navbar-nav > li > a {
  color: white !important;
  font-family: "Barlow";
  font-weight: 600;
  padding-top: 0;
  padding-bottom: 0;
}

.button__contact {
  display: block;
  background-color: white;
  border-radius: 30px;
  padding-left: 0.938rem;
  padding-right: 0.938rem;
}
.button__contact:hover {
  color: white;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}
.button__contact:hover > a {
  color: white !important;
}

.button__contact > a {
  color: #23303e !important;
  font-family: "Fraunces";
  font-size: 1.125rem;
  font-weight: bold;
}

.section__color {
  background-color: #3EBFFF;
  padding-top: 15rem;
}

.h1__title {
  color: white;
  font-family: "Fraunces";
  font-weight: 900;
  font-size: 4.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.img__arrow {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 7rem;
}

.img__backgroundorange {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  object-fit: cover;
  min-height: 100vh;
}

.section__eggimage {
  background-color: white;
}

.h2__eggTitle {
  font-family: "Fraunces";
  color: #23303e;
  font-weight: 900;
  font-size: 2.5rem;
}

.p__egDescription {
  font-family: "Barlow";
  color: #818498;
}

.btn__egg {
  font-family: "Fraunces";
  font-weight: 900;
  transform: 0.3s all ease-in;
}
.btn__egg:hover {
  border-bottom: 4px solid #fad400;
  border-radius: 3px;
}

.div__eggItens {
  margin-top: 14rem;
}

.img__egg {
  display: block;
  margin-left: auto;
}

.btn__standout {
  font-family: "Fraunces";
  font-weight: 900;
  transform: 0.3s all ease-in;
}
.btn__standout:hover {
  border-bottom: 5px solid #FE9A8D;
  border-radius: 3px;
}

.fig__images {
  position: relative;
  left: 0;
  top: 0;
}

.fig__title {
  font-family: "Fraunces";
  font-weight: 900;
  color: #25564b;
}

.fig__description {
  font-family: "Barlow";
  font-weight: 600;
  color: #25564b;
}

.test {
  margin-left: 250px;
}

body {
  font-size: 1.125rem;
}